html{height:100%}*{box-sizing:border-box}body{margin:0;height:100%;color:#2c3e50;font-family:Roboto Slab, Source Sans Pro, Helvetica, sans-serif;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;min-height:100vh}main{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-flex:1;-ms-flex:1;flex:1;max-width:700px;border-radius:5px;text-align:left;margin:3vh auto 6vh;font-size:14px;overflow:hidden}main a{color:#2c3e50;text-decoration:underline}.content{width:500px;padding:0 25px 25px;position:relative;overflow:hidden}@media all and (max-width: 700px){main{-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column}.content{width:100%;padding:0 0 25px}}a{color:#05b7ee;text-decoration:none}p{margin:0 0 10px}p:last-child{margin-bottom:0}.fadein-enter{-webkit-animation:fadein .5s;animation:fadein .5s}.fadein-leave{position:absolute;top:0;-webkit-animation:fadeout .5s;animation:fadeout .5s}@-webkit-keyframes fadein{0%{opacity:0;-webkit-transform:translateX(100%);transform:translateX(100%)}50%{}100%{opacity:1;-webkit-transform:translateX(0);transform:translateX(0)}}@keyframes fadein{0%{opacity:0;-webkit-transform:translateX(100%);transform:translateX(100%)}50%{}100%{opacity:1;-webkit-transform:translateX(0);transform:translateX(0)}}@-webkit-keyframes fadeout{0%{opacity:1;-webkit-transform:translateX(0);transform:translateX(0)}50%{}100%{opacity:0;-webkit-transform:translateX(-100%);transform:translateX(-100%)}}@keyframes fadeout{0%{opacity:1;-webkit-transform:translateX(0);transform:translateX(0)}50%{}100%{opacity:0;-webkit-transform:translateX(-100%);transform:translateX(-100%)}}



