




































.footer {
  border-top: 2px solid #05b7ee;
  background: #fefefe;
  padding: 20px;
}
.footer__list {
  max-width: 650px;
  margin: 0 auto;
  padding: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  list-style: none;
}
@media all and (max-width: 500px) {
  .footer__list {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    font-size: 12px;
  }
  .footer__item {
    padding-bottom: 3px;
  }
}
.footer__item {
  width: 100%;
  text-align: center;
}




